html { scroll-behavior: smooth; scroll-padding-top: 80px; /* Ativa a rolagem suave */ }

.align-wrapper {
  & > .text {
    margin-bottom: 30px;
    color: #f5f5f5;
    h2 {
      font-size: 30px;
      font-weight: bolder;
      line-height: normal;
      display: block;
      margin: 0 0 30px;
    }

    p {
      font-size: 23px;
      display: block;
      margin: 0;
      line-height: normal;
    }
  }
}



.card {
  flex: 1;
  perspective: 1000px;

  .bt-video {
    @include icomoon-icon('play-round');
    color: #FFF;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;

    &:before {
      font-size: 50px;
    }
  }

  .card-inner{
    transition: transform .5s;
    transform-style: preserve-3d;
    position: relative;
    width: 100%;
    height: 100%;
    transform: rotateY(0deg);

    &.active {
      transform: rotateY(180deg);
    }

    .card-back {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      backface-visibility: hidden;
      display: flex;
      justify-content: center;
      align-items: flex-start;;
      flex-direction: column;
    }
  }

  .card-front {
    width: 100%;
    height: 100%;
    position: relative;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      border-radius: 50px;
    }
  }

  .card-back{
    background: #7d2b8a;
    padding: 50px;
    border-radius: 50px;
    width: 100%;
    transform: rotateY(180deg);
  }

  .reverse {
    position: absolute;
    bottom: 5%;
    right: 5%;

    i:before {
      color: #FFF;
      font-size: 70px;
    }
  }

  .membro {
    display: flex;
    flex-direction: row;
    gap: 30px;
    margin-bottom: 30px;
    width: 100%;

    .avatar {
      width: 161px;
      height: 161px;
      flex-shrink: 0;

      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }

    .dados {
      display: flex;
      flex-direction: column;
      gap: 20px;

      strong {
        color: #84c0eb;
        font-size: 23px;
        display: block;
        margin-bottom: 10px;
        line-height: normal;
      }

      span {
        font-size: 18px;
        line-height: normal;
        color: #f5f5f5;
      }

      p {
        margin: 0;
        font-size: 16px;
        line-height: normal;
        color: #f5f5f5;
      }
    }
  }

  .info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;

    p {
      margin: 0;
      font-size: 16px;
      line-height: normal;
      color: #f5f5f5;
    }
  }
}


.video-thumbnails {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 50px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

/* Estilos da Modal */

.modal {
  display: none;
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0,0,0,0.8);
  padding-top: 60px;
}

.modal-content {
  background-color: #fefefe;
  margin: 5% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 60%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.close {
  position: absolute;
  right: 0;
  top: -10px;
  transform: translateY(-100%) translateX(0);

  i:before {
    color: #FFF;
  }
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%; /* Proporo 16:9 */
  height: 0;
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}






