@import '@styles/abstracts/_mixins.scss';

header {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
  background: #333380;

  body.contraste & {
    background: #000;
  }

  h1 {
    display: block;
    margin: 0 auto;
    width: 407px;
    height: 89px;

    a {
      width: 100%;
      height: 100%;
      display: block;
      background: url("@images/app-logo.png") no-repeat center top;
      background-size: cover;
      color: transparent;
      font-size: 0;
    }
  }
}


body {
  position: relative;

  .align-wrapper {
    width: 1280px;
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
  }


  #menu {
    display: flex;
    width: 100%;
    height: 80px;
    background: rgba(255, 255, 255, .95);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    position: sticky;
    top: 0;
    z-index: 9999;

    > * {
      flex: 1;
    }

    ul {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      list-style: none;
      margin: 0;
      padding: 0;
      flex: 1;

      li {
        display: flex;
        height: 100%;
        flex-direction: column;
        justify-content: center;
        margin-left: 30px;

        &:first-child {
          margin-left: 0;
        }

        a {
          padding: 0 10px;
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #333380;
          font-size: 18px;
          font-weight: bold;

          body.contraste & {
            color: yellow;
          }
        }
      }
    }
  }

  main {
    .main-content {
      overflow: hidden;

      > section {
        min-height: 72dvh;
        display: flex;
        flex-direction: column;

        .align-wrapper {
          padding: 50px !important;
        }
      }
    }
  }

  footer {
    display: block;
    background: #7f2a7e;
    width: 100%;

    .align-wrapper {
      padding: 50px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .row {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex: 1;
        justify-content: space-between;

        .column {
          width: fit-content;
          min-width: 42%;
          display: flex;
          flex-direction: column;
          position: relative;

          .text {
            color: #FFF;

            h3 {
              font-size: 18px;
              display: block;
              margin: 0;
            }

            p {
              display: block;
              font-size: 16px;
              margin-top: 30px;

              strong,
              span {
                display: block;
              }
            }
          }

        }
      }
    }

    .links-uteis {
      background: #2e2e82;

      .align-wrapper {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 25px 50px;

        .redes-sociais {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          gap: 50px;

          strong {
            font-size: 25px;
            color: #FFF;
          }

          ul {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            list-style: none;
            padding: 0;
            margin: 0;
            gap: 15px;

            li {
              a {
                transition: all ease-in-out .2s;
                width: 30px;
                height: 30px;
                display: block;
                font-size: 0;
                text-decoration: none;

                &:hover {
                  opacity: .8;
                }

                &:before {
                  font-size: 30px !important;
                  color: #FFF;
                }
              }

              &.facebook a {
                @include icomoon-icon('facebook');
              }

              &.instagram a {
                @include icomoon-icon('instagram');
              }

              &.twitter a {
                @include icomoon-icon('twitter');
              }

              &.youtube a {
                @include icomoon-icon('youtube');
              }

              &.whatsapp a {
                @include icomoon-icon('whatsapp');
              }

              &.linkedin a {
                @include icomoon-icon('linkedin');
              }
            }
          }
        }

        .mpf {
          h4 {
            a {
              width: 130px;
              height: 60px;
              display: block;
              background: url("@images/logo-mpf.svg") no-repeat center top;
              font-size: 0;
              color: transparent;
            }
          }
        }
      }
    }
  }
}

